@import (less) "color.less";
.tablet-sign-container{
  background-color: @lightest_gray;
}

.tablet-sign-container, .tablet-image-container, .tablet-text-container{
  height: 240px;
}


.tablet-image{
  display: inline-block;
  height: 140px;
  width: 217px;
  background-image: url("/cjlib/assets/images/immagine_tablet.png");
  background-repeat: no-repeat;
}

.vertical-alignment {
  transform:translateY(-50%);
  top:50%;
  position:absolute;
  &.orizontal-container {
    transform: translate(-50%,-50%);
    left:50%;
    position: absolute;
  }
}

h1 {
  font-size: 25px;
  margin:0;
  padding-bottom:15px;
  color:@black;
}

p {
  font-size: 16px;
  // font-weight: 700px;
  color:@gray;
  margin-bottom: 30px;
}
.button-container{
  width:25%;
}